
var api = require('../api');
var view = require('../view');
var email = require('../vm/email');
var message = require('../message');
var resolveObject = require('../resolve_object');

var template = "<h2>Email settings</h2>\n<!-- ko if: !permission() -->\n    <div class=\"alert alert-warning\" role=\"alert\">\n        You do not have permissions to edit email settings.\n    </div>\n<!-- /ko -->\n<!-- ko if: permission() -->\n    <!-- ko with: smtp -->\n        <div class=\"row bc-margin-top\">\n            <div class=\"col-md-2 col-sm-3 col-xs-4 text-right\">\n                <strong>Enabled</strong></div>\n            <div data-bind=\"text: (enabled ? 'yes' : 'no')\"\n                class=\"col-md-4 col-sm-5 col-xs-8\"></div>\n        </div>\n        <div class=\"row\">\n            <div class=\"col-md-2 col-sm-3 col-xs-4 text-right\">\n                <strong>SMTP host</strong></div>\n            <div data-bind=\"text: host\"\n                class=\"col-md-4 col-sm-5 col-xs-8\"></div>\n        </div>\n        <div class=\"row\">\n            <div class=\"col-md-2 col-sm-3 col-xs-4 text-right\">\n                <strong>SMTP auth</strong></div>\n            <div data-bind=\"text: auth\"\n                class=\"col-md-4 col-sm-5 col-xs-8\"></div>\n        </div>\n        <!-- ko if: auth === 'login' -->\n            <div class=\"row\">\n                <div class=\"col-md-2 col-sm-3 col-xs-4 text-right\">\n                    <strong>SMTP user</strong></div>\n                <div data-bind=\"text: (user === '' ? 'not set' : user)\"\n                    class=\"col-md-4 col-sm-5 col-xs-8\"></div>\n            </div>\n            <div class=\"row\">\n                <div class=\"col-md-2 col-sm-3 col-xs-4 text-right\">\n                    <strong>SMTP password</strong></div>\n                <div data-bind=\"text: (password === '' ? 'not set' : 'not shown')\"\n                    class=\"col-md-4 col-sm-5 col-xs-8\"></div>\n            </div>\n        <!-- /ko -->\n        <div class=\"row\">\n            <div class=\"col-md-2 col-sm-3 col-xs-4 text-right\">\n                <strong>SMTP security</strong></div>\n            <div data-bind=\"text: security\"\n                class=\"col-md-4 col-sm-5 col-xs-8\"></div>\n        </div>\n        <div class=\"row\">\n            <div class=\"col-md-2 col-sm-3 col-xs-4 text-right\">\n                <strong>SMTP from</strong></div>\n            <div data-bind=\"text: from\"\n                class=\"col-md-4 col-sm-5 col-xs-8\"></div>\n        </div>\n    <!-- /ko -->\n    <div class=\"row bc-margin-top\">\n        <div class=\"col-md-4 col-sm-5 col-xs-8 col-md-offset-2 col-sm-offset-3 col-xs-offset-4\">\n            <a href=\"#\" data-bind=\"click: edit\" class=\"btn btn-default\">\n                <span class=\"glyphicon glyphicon-pencil\"></span> Edit</a>\n        </div>\n    </div>\n    <!-- ko if: editing() -->\n        <hr>\n        <form data-bind=\"submit: save\" autocomplete=\"off\" id=\"smtp-config\">\n            <div class=\"checkbox\">\n                <label><input data-bind=\"checked: form.enabled\" type=\"checkbox\">\n                    Mail system enabled</label>\n            </div>\n            <div data-bind=\"errors: errors.host\" class=\"form-group\">\n                <label for=\"smtp-host\">SMTP host</label>\n                <input data-bind=\"textInput: form.host\"\n                    type=\"text\" class=\"form-control\" id=\"smtp-host\"\n                    placeholder=\"SMTP server hostname\">\n            </div>\n            <div class=\"form-group\">\n                <label for=\"smtp-auth\">SMTP authentication method</label>\n                <select data-bind=\"value: form.auth\" id=\"smtp-auth\"\n                    class=\"form-control\">\n                    <option value=\"plain\">Plain</option>\n                    <option value=\"login\">Login</option>\n                </select>\n            </div>\n            <div data-bind=\"errors: errors.user\" class=\"form-group\">\n                <label for=\"smtp-user\">SMTP user</label>\n                <input data-bind=\"textInput: form.user,\n                        disable: form.auth() === 'plain'\"\n                    type=\"text\" class=\"form-control\" id=\"smtp-user\"\n                    placeholder=\"SMTP server user\">\n            </div>\n            <div data-bind=\"errors: errors.password\" class=\"form-group\">\n                <label for=\"smtp-password\">SMTP password</label>\n                <input data-bind=\"textInput: form.password,\n                        disable: form.auth() === 'plain',\n                        attr: { type: password_text() ? 'text' : 'password' }\"\n                    type=\"password\" class=\"form-control\" id=\"smtp-password\"\n                    placeholder=\"SMTP server password\">\n            </div>\n            <div class=\"checkbox\">\n                <label><input data-bind=\"checked: password_text\" type=\"checkbox\">\n                    Show text</label>\n            </div>\n            <div class=\"form-group\">\n                <label for=\"smtp-security\">SMTP security</label>\n                <select data-bind=\"value: form.security\" id=\"smtp-security\"\n                    class=\"form-control\">\n                    <option value=\"none\">None</option>\n                    <option value=\"ssl\">SSL</option>\n                    <option value=\"tls\">TLS</option>\n                    <option value=\"starttls\">STARTTLS</option>\n                </select>\n            </div>\n            <div data-bind=\"errors: errors.from\" class=\"form-group\">\n                <label for=\"smtp-from\">Notification sender email (From)</label>\n                <input data-bind=\"textInput: form.from\"\n                    type=\"text\" class=\"form-control\" id=\"smtp-form\"\n                    placeholder=\"Mail sender address\">\n            </div>\n            <div class=\"alert alert-info\" role=\"alert\">\n                New SMTP settings should be tested before saving. The\n                test email will be sent to the current user's mail address.\n            </div>\n            <!-- ko if: !!error() -->\n                <div data-bind=\"text: error\" class=\"alert alert-danger\" role=\"alert\"></div>\n            <!-- /ko -->\n            <!-- ko if: !!info() -->\n                <div data-bind=\"text: info\" class=\"alert alert-success\" role=\"alert\"></div>\n            <!-- /ko -->\n            <div data-bind=\"errors: errors.subject\" class=\"form-group\">\n                <label for=\"smtp-subject\">Test mail subject</label>\n                <input data-bind=\"textInput: form.subject\"\n                    type=\"text\" class=\"form-control\" id=\"smtp-subject\"\n                    placeholder=\"Test mail subject\">\n            </div>\n            <div data-bind=\"errors: errors.body\" class=\"form-group\">\n                <label for=\"smtp-body\">Test mail body</label>\n                <textarea data-bind=\"textInput: form.body\"\n                    placeholder=\"Test mail body\" rows=\"3\"\n                    class=\"form-control\" id=\"smtp-body\"></textarea>\n            </div>\n            <button type=\"submit\" class=\"btn btn-default\">\n                <span class=\"glyphicon glyphicon-ok\"></span> Save\n            </button>\n            <a data-bind=\"click: test\" class=\"btn btn-default\" href=\"#\">\n                <span class=\"glyphicon glyphicon-envelope\"></span> Test</a>\n            <a data-bind=\"click: cancel\" class=\"btn btn-default\" href=\"#\">\n                <span class=\"glyphicon glyphicon-remove\"></span> Cancel</a>\n        </form>\n    <!-- /ko -->\n<!-- /ko -->\n";

// Page for editing email settings.

exports.create = function(id) {

    var model = {

        email: ko.observable(),

        permission: ko.observable(false),

        editing: ko.observable(false),

        smtp: ko.observable({}),

        form: {

            enabled: ko.observable(false),

            host: ko.observable(''),

            user: ko.observable(''),

            password: ko.observable(''),

            auth: ko.observable('login'),

            security: ko.observable('none'),

            from: ko.observable('admin@example.com'),

            subject: ko.observable('Test mail'),

            body: ko.observable('Test mail body')
        },

        errors: {

            host: ko.observableArray([]),

            user: ko.observableArray([]),

            password: ko.observableArray([]),

            from: ko.observableArray([]),

            subject: ko.observableArray([]),

            body: ko.observableArray([])
        },

        // Test error.

        error: ko.observable(),

        // Test info message.

        info: ko.observable(),

        // Flag to show password unmasked.

        password_text: ko.observable(false)
    };

    // Saves settings and updates the
    // read-only views of options.

    model.save = function(form) {

        clearErrors();

        validateParams();

        // Checks whether some input
        // has error set.

        if (!checkError(form)) {

            return false;
        }

        var smtp = toSmtp(model.form);

        api.updateConfig(toList(smtp)).then(function() {

            model.smtp(smtp);

            message.info('Mailing system parameters have been updated.');

            route.refresh();

        }).catch(message.error);
    };

    model.edit = function() {

        model.editing(true);
    };

    // Tests the settings that are currently
    // set in the editing form.

    model.test = function() {

        model.info(undefined);

        model.error(undefined);

        clearErrors();

        validateParams();

        validateTest();

        var form = document.getElementById('smtp-config');

        // Checks whether some input
        // has error set.

        if (!checkError(form)) {

            return false;
        }

        var smtp = toSmtp(model.form);

        api.testMail(smtp).then(function() {

            model.info('Test mail sent successfully.');

        }).catch(function(err) {

            model.error('Mail testing failed: ' +
                (err.jsendMessage ? err.jsendMessage : err.toString()));
        });
    };

    // Cancels editing. Refreshes current page.

    model.cancel = function() {

        route.refresh();
    };

    // Clears current form errors.

    function clearErrors() {

        Object.keys(model.errors).forEach(function(key) {

            model.errors[key]([]);
        });
    }

    // Validates SMTP parameters.

    function validateParams() {

        var form = model.form;

        if (form.host().match(/^\s*$/)) {

            model.errors.host.push('SMTP host name must be set.');
        }

        if (form.auth() === 'login') {

            if (form.user().match(/^\s*$/)) {

                model.errors.user.push('SMTP user must be set for SMTP auth.');
            }

            if (form.password().match(/^\s*$/)) {

                model.errors.password.push('SMTP password must be set for SMTP auth.');
            }
        }

        if (form.from().match(/^\s*$/)) {

            model.errors.from.push('Notification sender mail must be set.');

        } else {

            if (!form.from().match(/^[^@]+@[^@]+$/)) {

                model.errors.from.push('Notification sender mail must be a valid mail address.');
            }
        }
    }

    // Validates test parameters.

    function validateTest() {

        var form = model.form;

        if (form.subject().match(/^\s*$/)) {

            model.errors.subject.push('Test mail subject must be set.');
        }

        if (form.body().match(/^\s*$/)) {

            model.errors.body.push('Test mail body must be set.');
        }
    }

    // Checks for errors on the form
    // elements. Focuses on the first
    // input with an error.

    function checkError(form) {

        var input = form.querySelector(
            '.has-error input, .has-error textarea,' +
            ' .has-error checkbox, .has-error select');

        if (input) {

            input.focus();

            return false;
        }

        return true;
    }

    return api.userInfo().then(function(info) {

        if (info.type !== 'admin') {

            view.show(template, model);

        } else {

            return api.config().then(function(config) {

                var smtp = extract(config);

                model.smtp(smtp);

                toForm(smtp, model.form);

                model.permission(true);

                view.show(template, model);
            });
        }
    });
};

// Extracts SMTP settings from
// generic config entries.

function extract(config) {

    var obj = {};

    config.forEach(function(entry) {

        obj[entry.name] = entry.value;
    });

    return {

        enabled: obj.smtp_enabled,

        host: obj.smtp_host,

        user: obj.smtp_user,

        password: obj.smtp_password,

        auth: obj.smtp_auth,

        security: obj.smtp_security,

        from: obj.smtp_from
    };
}

// Sets form values based on
// smtp values.

function toForm(smtp, form) {

    Object.keys(smtp).forEach(function(key) {

        form[key](smtp[key]);
    });
}

// Creates new object with
// values from the form binding
// object.

function toSmtp(form) {

    var smtp = {};

    Object.keys(form).forEach(function(key) {

        smtp[key] = form[key]();
    });

    return smtp;
}

// Turns the SMTP configuration
// object into a list of configuration
// entries.

function toList(smtp) {

    return [

        { name: 'smtp_enabled', value: smtp.enabled },
        { name: 'smtp_host', value: smtp.host },
        { name: 'smtp_user', value: smtp.user },
        { name: 'smtp_password', value: smtp.password },
        { name: 'smtp_auth', value: smtp.auth },
        { name: 'smtp_security', value: smtp.security },
        { name: 'smtp_from', value: smtp.from }
    ];
}
